.tweet {
  position: relative;

  list-style: none;
  width: 380px;
  height: 460px;
  background: linear-gradient(
    114.99deg,
    #471ca9 -0.99%,
    #5736a3 54.28%,
    #4b2a99 78.99%
  );
  box-shadow: -2.5777px 6.87386px 20.6216px rgba(0, 0, 0, 0.23);
  border-radius: 20px;

  padding: 28px 36px 36px 36px;
}

.logo {
  position: absolute;
  top: 20px;
  left: 20px;
}

.line {
  position: absolute;

  width: 380px;
  height: 8px;
  left: 0px;
  top: 214px;

  background: #ebd8ff;
  box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.06),
    inset 0px -1.71846px 3.43693px #ae7be3, inset 0px 3.43693px 2.5777px #fbf8ff;
}

.avatarWrapper {
  position: absolute;
  top: 178px;
  left: 150px;
  z-index: 2;

  width: 80px;
  height: 80px;
  box-shadow: 0px 4.39163px 4.39163px rgba(0, 0, 0, 0.06),
    inset 0px -2.19582px 4.39163px #ae7be3,
    inset 0px 4.39163px 3.29372px #fbf8ff;
  border-radius: 50%;
}

.avatar {
  position: absolute;
  top: 178px;
  left: 150px;

  width: 80px;
  height: 80px;
  border: 8px solid #ebd8ff;
  border-radius: 50%;
  background-color: #5736a3;
}

.textTweets {
  margin: 88px auto 16px auto;
  min-width: 132px;

  font-family: 'Montserrat';

  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  text-transform: uppercase;

  color: #ebd8ff;
}

.textFollowers {
  margin: 0 auto 26px auto;
  min-width: 214px;

  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  text-transform: uppercase;

  color: #ebd8ff;
}

.button {
  display: block;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 14px 28px;

  width: 196px;
  height: 50px;

  background: #ebd8ff;
  box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
  border-radius: 10.3108px;
  border: 0;

  font-family: 'Montserrat';

  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  text-transform: uppercase;
  color: #373737;
}

.button:hover,
.button:focus {
  box-shadow: 0 0 10px rgba(218, 212, 212, 0.601);
}

.buttonActive {
  display: block;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 14px 28px;

  width: 196px;
  height: 50px;

  background: #5cd3a8;
  box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
  border-radius: 10.3108px;
  border: 0;

  font-family: 'Montserrat';

  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  text-transform: uppercase;
  color: #373737;
}
